import {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  tableCellClasses,
  Link,
  styled,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  Typography,
} from "@mui/material";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import SendIcon from "@mui/icons-material/Send";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import { MyTheme } from "../ThemeOptions";
import React, { useState } from "react";

const StyledTableCell = styled(TableCell)({
  paddingRight: "5%",
});

const StyleContact = { fontSize: 60, color: MyTheme.palette.primary.main };

const socialMedia = {
  LinkedIn: {
    URL: "https://www.linkedin.com/in/edvardas-vabuolas/",
    Message:
      "Would you like to visit Edvardas Vabuolas' LinkedIn profile in a new tab?",
  },
  GitHub: {
    URL: "https://github.com/edvardas-vabuolas-personal",
    Message:
      "Would you like to open Edvardas Vabuolas' GitHub profile in a new tab?",
  },
  Email: {
    URL: "mailto:edvardas.vabuolas@gmail.com",
    Message:
      "Would you like to open your email client to send a message to Edvardas Vabuolas?",
  },
  WhatsApp: {
    URL: "https://api.whatsapp.com/send?phone=+447523633855&text=Hi Edvardas, I'm exploring your data science portfolio. Let's chat!",
    Message:
      "Would you like to open WhatsApp in a new tab and send a message to Edvardas Vabuolas?",
  },
  Phone: {
    URL: "tel:+447523633855",
    Message:
      "Would you like to call Edvardas Vabuolas? Your device will initiate the call.",
  },
};

type SocialMediaType = keyof typeof socialMedia;

const StyledTableContainer = styled(TableContainer)(({ theme }) => ({
  cursor: "pointer",
  padding: "8px 16px",
  backgroundColor: theme.palette.background.paper,
  color: theme.palette.primary.main,
  borderRadius: "4px",
  "&:hover": {
    backgroundColor: "rgba(255, 255, 255, .1)",
  },
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  alignContent: 'center'
}));

export function ContactDetails() {
  const [isConfirmationOpen, setConfirmationOpen] = useState(false);
  const [socialMediaSelected, selectSocialMedia] =
    useState<SocialMediaType>("LinkedIn");

  const handleLinkClick = (e: any, socMedia: SocialMediaType) => {
    selectSocialMedia(socMedia);
    setConfirmationOpen(true);
    e.stopPropagation(); // Prevent the event from reaching the parent ButtonBase
  };

  const handleConfirmation = (e: any, confirmed: any) => {
    if (confirmed) {
      window.open(socialMedia[socialMediaSelected].URL, "_blank");
    }
    setConfirmationOpen(false);
    e.stopPropagation();
  };

  return (
    <StyledTableContainer>
      <Table
        sx={{
          [`& .${tableCellClasses.root}`]: {
            borderBottom: "none",
          },
        }}
      >
        <TableHead>
          <TableRow>
            <TableCell></TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableRow>
          <StyledTableCell>
            <LinkedInIcon style={StyleContact} />
          </StyledTableCell>
          <StyledTableCell>
            <Link
              onClick={(e) => handleLinkClick(e, "LinkedIn")}
              style={StyleContact}
            >
            LinkedIn
            </Link>
          </StyledTableCell>
        </TableRow>
        <TableRow>
          <StyledTableCell>
            <GitHubIcon style={StyleContact} />
          </StyledTableCell>
          <StyledTableCell>
            <Link
              onClick={(e) => handleLinkClick(e, "GitHub")}
              style={StyleContact}
            >
              GitHub
            </Link>
          </StyledTableCell>
        </TableRow>
        <TableRow>
          <StyledTableCell>
            <SendIcon style={StyleContact} />
          </StyledTableCell>
          <StyledTableCell>
            <Link
              onClick={(e) => handleLinkClick(e, "Email")}
              style={StyleContact}
            >
              Email
            </Link>
          </StyledTableCell>
        </TableRow>
        <TableRow>
          <StyledTableCell>
            <WhatsAppIcon style={StyleContact} />
          </StyledTableCell>
          <StyledTableCell>
            <Box
              style={{
                display: "flex",
                flexDirection: "column",
              }}
            >
              <Link
                onClick={(e) => handleLinkClick(e, "WhatsApp")}
                style={StyleContact}
              >
                WhatsApp
              </Link>
              <Link
                onClick={(e) => handleLinkClick(e, "Phone")}
                style={{ fontSize: 24, color: MyTheme.palette.primary.main }}
              >
                (+44 7523 633855)
              </Link>
            </Box>
          </StyledTableCell>
        </TableRow>
      </Table>
      <Dialog
        open={isConfirmationOpen}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Continue the action?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {socialMedia[socialMediaSelected].Message}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={(e) => handleConfirmation(e, false)}>Cancel</Button>
          <Button onClick={(e) => handleConfirmation(e, true)} autoFocus>
            Continue
          </Button>
        </DialogActions>
      </Dialog>
    </StyledTableContainer>
  );
}
