import { GitHub } from "@mui/icons-material";
import { styled } from "@mui/material";
import { useState } from "react";

const GithubIconStyled = styled(GitHub)(({ theme }) => ({
  position: "fixed",
  top: "10px",
  right: "10px",
  color: theme.palette.background.paper,
  fontSize: 60,
  cursor: "pointer",
  zIndex: 100,
}));
const GithubIconStyles = {
  Still: {
    transform: "rotate(0deg)",
    transition: "1s",
  },
  Hovered: {
    transform: "rotate(-360deg)",
    transition: "2s",
  },
};

export function GihubIconInteractive() {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  const handleOnClickGHIcon = (e: any) => {
    window.open("https://github.com/edvardas-vabuolas-personal", "_blank");
    e.stopPropagation();
  };
  return (
    <div
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onClick={handleOnClickGHIcon}
    >
      <GithubIconStyled
        sx={isHovered ? GithubIconStyles.Still : GithubIconStyles.Hovered}
      />
    </div>
  );
}
