import {
  Card,
  CardContent,
  Chip,
  Link,
  Stack,
  Typography,
} from "@mui/material";
import { projectsData } from "../Data/projectsData";
import { projectsDataType } from "../Types/types";
import { StyledCardMedia } from "./StyledCardMedia";
import { DataScienceSkills, TechnicalSkills } from "./SkillsChips";

export function ProjectList() {
  return (
    <div className="projects-global">
      <div className="project-div">
        {projectsData.map((project: projectsDataType) => (
          <Card sx={{ width: "45%" }} id={project.ID}>
            <StyledCardMedia project={project} />

            <CardContent>
              <TechnicalSkills project={project} />
              <DataScienceSkills project={project} />
              <Typography
                variant="h5"
                component="div"
                sx={{ marginBottom: "0.75em" }}
              >
                <Link href={project.ReadMoreURL} target="_self">
                  {project.Header}
                </Link>
              </Typography>

              <Typography variant="body2" color="text.secondary">
                {project.Preview.length > 560
                  ? `${project.Preview.slice(0, 560)}...`
                  : project.Preview}
                <Link href={project.ReadMoreURL} >
                  <Typography sx={{marginTop: '0.5em'}}>Read more</Typography>
                </Link>
              </Typography>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
