import { GitHub, Reply } from "@mui/icons-material";
import { styled } from "@mui/material";
import { useState } from "react";

const GoBackStyled = styled(Reply)(({ theme }) => ({
  position: "fixed",
  top: "10px",
  left: "10px",
  color: theme.palette.background.paper,
  fontSize: 60,
  cursor: "pointer",
  zIndex: 100,
}));
const GoBackStyles = {
  Still: {
    transform: "rotate(15deg)",
    transition: "0.2s",
  },
  Hovered: {
    transform: "rotate(0deg)",
    transition: "0.5s",
  },
};

export function GoBackInteractive() {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  const handleOnClickGBIcon = (e: any) => {
    window.open("#/portfolio_public/", "_self");
    e.stopPropagation();
  };
  return (
    <div
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onClick={handleOnClickGBIcon}
    >
      <GoBackStyled
        sx={isHovered ? GoBackStyles.Still : GoBackStyles.Hovered}
      />
    </div>
  );
}
