import {
  Avatar,
  ButtonBase,
  Fade,
  Tooltip,
  Typography,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import Badge from "@mui/material/Badge";
import { ContactDetails } from "./Contact";
import React, { useState } from "react";
import "../App.css";

let currentHour = new Date().getHours();

const workingHours: { start: number; end: number } = { start: 8, end: 22 };

const messages: { freeMsg: string; busyMsg: string } = {
  freeMsg: "Hi! Let's have a chat.",
  busyMsg: `Hi! I'm available for a chat between ${workingHours.start}:00 and ${workingHours.end}:00. Feel free to drop me a message!`,
};

const isWorkingHours = () =>
  currentHour >= workingHours.start && currentHour <= workingHours.end;

const StyledBadge = styled(Badge)(({ theme }) => ({
  "& .MuiBadge-badge": {
    backgroundColor: isWorkingHours() ? "#44b700" : "#bfbfbf",
    color: isWorkingHours() ? "#44b700" : "#bfbfbf",
    width: "20%",
    height: "20%",
    borderRadius: "50%",
    boxShadow: `0 0 0 10px ${theme.palette.background.paper}`,
    "&::after": {
      position: "r",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      borderRadius: "50%",
      animation: "ripple 1.2s infinite ease-in-out",
      border: `${isWorkingHours() ? 1 : 0}px solid currentColor`,
      content: '""',
    },
  },
  "@keyframes ripple": {
    "0%": {
      transform: "scale(.8)",
      opacity: 1,
    },
    "100%": {
      transform: "scale(2.4)",
      opacity: 0,
    },
  },
}));

export function BadgeAvatar() {
  const [avatarVisible, setAvatarVisible] = useState(true);
  const [contactDetails, setContactDetails] = useState(false);
  const handleBadgeClick = () => {
    setAvatarVisible(!avatarVisible);
    if (!contactDetails) {
      setTimeout(() => {
        setContactDetails(!contactDetails);
      }, 1000);
    } else {
      setContactDetails(!contactDetails);
    }
  };

  return (
    <ButtonBase
      onClick={handleBadgeClick}
      disableRipple
      sx={{
        // outline: "1px black solid",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div className={`avatar-container ${!avatarVisible ? "hidden" : ""}`}>
        <Tooltip
          title={
            <Typography fontSize={24} justifyContent={'center'} textAlign={'center'}>
              {messages[isWorkingHours() ? "freeMsg" : "busyMsg"]}
            </Typography>
          }
          arrow
          TransitionComponent={Fade}
          TransitionProps={{ timeout: 150 }}
          componentsProps={{
            tooltip: {
              sx: {
                display: avatarVisible ? 'block' : 'none',
                bgcolor: "#effdef",
                "& .MuiTooltip-arrow": {
                  color: "#effdef",
                },
                color: "#414141",
              },
            },
          }}
        >
          <StyledBadge
            overlap="circular"
            anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
            variant="dot"
          >
            <Avatar
              alt="Edvardas Vabuolas"
              src="MyPhoto.jpg"
              sx={{ width: "400px", height: "400px" }}
            />
          </StyledBadge>
        </Tooltip>
      </div>
      {contactDetails && <ContactDetails />}
    </ButtonBase>
  );
}
