import { useState } from "react";
import { projectsDataType } from "../Types/types";
import { Button } from "@mui/material";

export function StyledCardMedia({ project }: { project: projectsDataType }) {
    const [isHovered, setIsHovered] = useState(false);
  
    const handleMouseEnter = () => {
      setIsHovered(true);
    };
  
    const handleMouseLeave = () => {
      setIsHovered(false);
    };
  
    const handleButtonClick = (e: any) => {
      window.open(project.URL, '_blank')
      e.stopPropagation();
    }
  
    return (
      <div
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        style={{
          position: "relative",
          height: "400px",
          overflow: "hidden",
        }}
      >
        <img
          alt={project.Header}
          src={project.Media}
          style={{
            objectFit: "cover",
            filter: isHovered ? "brightness(60%) blur(1px)" : "none",
            transition: "filter 0.3s",
            width: "100%",
            height: "100%",
          }}
        />
        {isHovered && (
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              padding: "10px",
              borderRadius: "4px",
            }}
          >
            <Button size="large" onClick={handleButtonClick}>{project.ButtonText}</Button>
          </div>
        )}
      </div>
    );
  }