import Timeline from "@mui/lab/Timeline";
import TimelineItem from "@mui/lab/TimelineItem";
import TimelineSeparator from "@mui/lab/TimelineSeparator";
import TimelineConnector from "@mui/lab/TimelineConnector";
import TimelineContent from "@mui/lab/TimelineContent";
import TimelineDot from "@mui/lab/TimelineDot";
import { styled } from "@mui/material/styles";
import {
  Box,
  Container,
  Divider,
  List,
  ListItem,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import KeyboardReturnIcon from "@mui/icons-material/KeyboardReturn";
import { employmentHistory } from "./cv";

const StyledTimelineContent = styled(TimelineContent)(({ theme }) => ({
  ...theme.typography.body2,
  color: theme.palette.primary.main,
  display: "flex", // Use flexbox for centering
  flexDirection: "column", // Stack content vertically
}));

const StyledTimelineDot = styled(TimelineDot)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main,
}));

const StyledTimelineConnector = styled(TimelineConnector)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main,
  height: "100px",
}));

const TimelineButton = styled(Box)(({ theme }) => ({
  cursor: "pointer",
  padding: "8px 16px",
  backgroundColor: theme.palette.background.paper,
  color: theme.palette.primary.main,
  borderRadius: "4px",
  "&:hover": {
    backgroundColor: "rgba(255, 255, 255, .1)",
  },
}));

const ReturnButton = styled(Box)(({ theme }) => ({
  cursor: "pointer",
  padding: "8px 16px",
  backgroundColor: theme.palette.background.paper,
  color: theme.palette.primary.main,
  borderRadius: "4px",
  "&:hover": {
    backgroundColor: "rgba(255, 255, 255, .1)",
  },
  width: "8vw",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
}));

const StyledRecord = styled(Container)(({ theme }) => ({
  paddingTop: "5%",
  paddingBottom: "5%",
  paddingLeft: 0,
  color: theme.palette.text.primary,
}));

function EmploymentRow({
  role,
  employer,
  dates,
  onClickFn,
}: {
  role: string;
  employer: string;
  dates: string;
  onClickFn: any;
}) {
  return (
    <StyledTimelineContent>
      <TimelineButton onClick={onClickFn}>
        <Typography>{role}</Typography>
        <Typography>{employer}</Typography>
        <Typography>{dates}</Typography>
      </TimelineButton>
    </StyledTimelineContent>
  );
}

const HeaderAndDetails = (
  employmentRow: number | undefined,
  onClickFn: any,
  side: string
) => [
  <Container
    sx={{
      display: "block",
      width: "80%",
      textAlign: `${side === "right" ? "start" : "right"}`,
    }}
  >
    <Typography sx={{ fontSize: 18 }}>
      {employmentHistory[employmentRow as number].role}
    </Typography>
    <Typography sx={{ fontSize: 18 }}>
      {employmentHistory[employmentRow as number].employer}
    </Typography>
    <Typography sx={{ fontSize: 18 }}>
      {employmentHistory[employmentRow as number].dates}
    </Typography>
  </Container>,
  <Container sx={{ display: "block", width: "20%" }}>
    <ReturnButton>
      <KeyboardReturnIcon
        style={{
          fontSize: 60,
          transform: `${side === "right" ? "scaleX(1)" : "scaleX(-1)"}`,
        }}
        onClick={onClickFn}
      />
    </ReturnButton>
  </Container>,
];

const SepAndDetails = (
  employmentRow: number | undefined,
  onClickFn: any,
  side: string
) => [
  <TimelineSeparator sx={{ height: "67vh" }}>
    <StyledTimelineDot />
    <StyledTimelineConnector />
  </TimelineSeparator>,
  <div>
    <Container
      sx={{
        marginBottom: 2,
        display: "flex",
        flexDirection: "row",
        outline: "0px red yellow",
        justifyContent: "space-around",
        alignContent: "center",
      }}
    >
      {side === "right"
        ? HeaderAndDetails(employmentRow, onClickFn, side)
        : HeaderAndDetails(employmentRow, onClickFn, side).reverse()}
    </Container>
    {/* <Divider/> */}
    <Container sx={{ marginTop: "1em", minHeight: "30vh" }}>
      {employmentHistory[employmentRow as number].detailedDescription}
    </Container>
  </div>,
];

function DetailedDescription({
  onClickFn,
  employmentRow,
  side,
}: {
  onClickFn: any;
  employmentRow: number | undefined;
  side: string;
}) {
  return (
    <StyledRecord>
      <Box sx={{ display: "flex" }}>
        {side === "right"
          ? SepAndDetails(employmentRow, onClickFn, side)
          : SepAndDetails(employmentRow, onClickFn, side).reverse()}
      </Box>
    </StyledRecord>
  );
}

export default function AlternateTimeline() {
  const [isExpanded, setExpanded] = useState(false);
  const [employmentRow, setEmploymentRow] = useState<number | undefined>(
    undefined
  );
  const [side, setSide] = useState<string>("right");

  const handleExpansion = (e: any, side: string, employmentRow?: number) => {
    setExpanded(!isExpanded);
    setEmploymentRow(employmentRow);
    setSide(side);
  };

  return !isExpanded ? (
    <Timeline position="alternate" sx={{paddingTop: '3vw'}}>
      {employmentHistory.map((record: any, index: number) => (
        <TimelineItem>
          <TimelineSeparator>
            <StyledTimelineDot />
            {index < employmentHistory.length - 1 ? (
              <StyledTimelineConnector />
            ) : (
              false
            )}
          </TimelineSeparator>
          <EmploymentRow
            role={record["role"]}
            employer={record["employer"]}
            dates={record["dates"]}
            onClickFn={(e: any) => handleExpansion(e, record["side"], index)}
          />
        </TimelineItem>
      ))}
    </Timeline>
  ) : (
    <DetailedDescription
      onClickFn={handleExpansion}
      employmentRow={employmentRow}
      side={side}
    />
  );
}
